*,
::after,
::before {
 box-sizing:unset !important;
}
body {
    line-height:unset !important;
}
.board {
    font-size: unset;
    font-weight: unset;    
    text-align: left;
    width: 315px;
    height: 315px;
}
.avail {
    font-size: smaller;    
}
.availnums{
    font-size: small;
    font-weight: bold;
}
.none{
    display: none !important;
}
.subset2{
    background-color : cornflowerblue;
    font-size: .5rem;
}
.subset3{
    background-color : lightgreen;
    font-size: .5rem;
}
.unique{
    background-color : orange;
}
.single{
    background-color : lightgreen;
}
.selected{
    background-color : blue;
}
.hiddensubset2{
    background-color: cornflowerblue;
    font-size: .6rem;
}
.hiddensubset3{
    background-color: lightgreen;
    font-size: .55rem;
}
.removedNumber{
    color: red;
}
.canRemove{
    font-size: .55rem;
}
.highlightBtns{
    font-size: .95rem !important;
    padding: 3px !important;
    margin: 2px !important;
    
}